@use "." as this;
@use "@app/bancor-components/Element/Text";
@use "@app/bancor-components/Element/Button";
@use "@app/bancor-components/Element/Link";
@use "@app/bancor-components/Style/screen";
@use "@app/bancor-components/Style/Color";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Element/Form/Input";
@use "@app/bancor-components/Element/Image";
@use "@app/bancor-components/Style/Layout/Display";
@use "@app/bancor-components/Style/Font";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Layout/Padding";

#{this.$selector} {
    .gap {
        @include Margin.set($bottom: gamma);
    }

    .alert {
        @include Flex.set($align: center);

        #{Text.$selector} {
            @include Color.set($fg: "gray-dark");
        }

        .alertIcon {
            @include Flex.set($shrink: none);
            @include Margin.set($right: alpha);
        }
    }

    .bancorKey#{Link.$selector} {
        @include Button.asUnderlineLink;

        #{Text.$selector} {
            @include Font.set($align: "left");
        }
    }

    .info {
        @include Margin.set($top: gamma, $bottom: gamma);
        @include screen.lg {
            @include Margin.set(auto, $top: gamma, $bottom: gamma);
            width: Layout.get-width(iota);
        }
    }

    .icon {
        @include Flex.set($justify: center);
        @include Display.set($display: flex);
        @include Margin.set($top: beta, $bottom: beta);
    }

    .contentModal {
        @include Margin.set($right: gamma, $left: gamma);
    }

    .buttonSoftToken {
        @include Margin.set($top: delta, $right: auto, $left: auto, $bottom: delta);
        @include Flex.set($dir: column);
        @include screen.lg {
            width: Layout.get-width(iota);
        }
    }

    #{Input.$selector} {
        @include Margin.set($bottom: delta, $top: delta);
    }

    .row {
        @include Flex.set($justify: between);
        @include Margin.set($top: beta);
    }

    .label {
        @include Color.set($fg: "dark-gray");
        @include Font.set($family: heading, $weight: bold, $transform: capitalize);
        @include Flex.set($align: start);
    }

    .title#{Text.$selector} {
        @include Color.set($fg: "gray-dark");
        @include Font.set($size: delta, $weight: default, $family: "default", $align: default);
        @include Margin.set($bottom: gamma, $top: gamma);
    }

    .device {
        @include Font.set($align: default);
    }

    .transactionCode {
        @include Font.set($transform: upper);
    }

    .stepList {
        @include Margin.set($top: 0, $right: auto, $bottom: gamma, $left: auto);
        @include Display.set($display: table);
        list-style: decimal;
    }

    .stepList li::marker {
        @include Font.set($family: default, $size: gamma);
        @include Color.set($fg: "gray-light");
    }

    .linkOtpTitle {
        @include Font.set($family: heading, $weight: bold, $size: iota);
        @include Margin.set($bottom: gamma);
    }

    .linkOtpDescription {
        @include Font.set($weight: bold, $size: delta);
    }

    .linkOtpList {
        @include Margin.set($top: beta, $bottom: gamma);
        @include Font.set($family: default);
        list-style: decimal;
        #{Text.$selector} {
            @include Color.set($fg: "gray-dark");
        }
    }

    .linkOtpIcon {
        @include Margin.set($left: auto, $right: auto, $bottom: gamma, $top: gamma);
    }

    .linkOtpFooter#{Text.$selector} {
        @include Color.set($fg: "gray-dark");
        @include Font.set($weight: bold, $size: delta, $align: center);
    }

    .footer {
        width: Layout.get-max-width(alpha);
        #{Button.$selector}:last-child {
            @include Margin.set($top: beta);
        }
    }
}
